@font-face {
  font-family: 'Lato';
  src: url("../fonts/Lato-Black.woff2") format("woff2");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Lato';
  src: url("../fonts/Lato-Heavy.woff2") format("woff2");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Lato';
  src: url("../fonts/Lato-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Roboto Slab';
  src: url("../fonts/RobotoSlab-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Lato';
  src: url("../fonts/Lato-HeavyItalic.woff2") format("woff2");
  font-weight: 900;
  font-style: italic;
  font-display: swap;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  color: #4b5157;
  background-color: #E5E5E5;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
}

button {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1170px;
}

.header {
  background-color: #262D33;
  padding: 15px 0;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  width: 163px;
  height: 40px;
}

.nav {
  color: #fff;
}

.nav__list {
  display: flex;
}

.nav__link {
  font-family: "Lato";
  font-weight: 900;
  font-size: 14px;
  padding: 25px 15px;
}

.search {
  position: relative;
  width: 263px;
  height: 39px;
  background-color: #656B70;
  border-radius: 10px;
  overflow: hidden;
}

.search__input {
  outline: none;
  padding-left: 23px;
  padding-right: 75px;
  padding: 9px 75px 9px 25px;
  width: 100%;
  height: 100%;
  border: none;
  background-color: transparent;
}

.search__input::placeholder {
  color: #939699;
  font-size: 14px;
  line-height: 20px;
}

.search__button {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 71px;
}

.search__button:hover {
  background-color: #4d5155;
}

.search__button-img {
  width: 17.5px;
  height: 18px;
}

.hero__bg {
  position: absolute;
  background-color: #262D33;
  height: 475px;
  width: 100%;
  margin-top: -30px;
}

.hero__wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 570px;
  margin-top: 30px;
  margin-bottom: 30px;
  border-radius: 10px;
  overflow: hidden;
}

.hero__content {
  position: relative;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  padding: 55px 55px 100px 55px;
  background-image: url("../img/hero-1.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #939699;
  z-index: 10;
}

.hero__content::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, rgba(64, 48, 61, 0.35), rgba(64, 48, 61, 0.35));
  z-index: -1;
}

.hero__content--active {
  display: flex;
}

.hero__content-subtitle {
  margin-bottom: 21px;
  font-weight: 900;
  font-size: 12px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: #4592ff;
}

.hero__content-title {
  max-width: 70%;
  margin-bottom: 49px;
  font-family: 'Roboto Slab', sans-serif;
  font-size: 40px;
  line-height: 50px;
  color: #fff;
}

.hero__sidebar {
  height: 100%;
  max-width: 263px;
  background-color: #fff;
}

.hero__sidebar-title {
  padding: 23px 25px 20px;
  font-weight: 900;
  font-size: 14px;
  color: #262D33;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

.hero__sidebar-item {
  height: 100px;
  border-top: 1px solid #D9DADB;
}

.hero__sidebar-item:last-child div {
  height: 112%;
}

.hero__sidebar-link {
  padding: 19px 25px 19px;
  display: block;
  height: 100%;
  cursor: pointer;
}

.hero__sidebar-link--active {
  background-color: #F5F5F5;
}

.hero__sidebar-link:hover {
  background-color: #F5F5F5;
}

.hero__sidebar-subtitle {
  margin-bottom: 9px;
  font-weight: 900;
  font-size: 12px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

.hero__sidebar-subtitle--1 {
  color: #3BBDC4;
}

.hero__sidebar-subtitle--2 {
  color: #6E99AE;
}

.hero__sidebar-subtitle--3 {
  color: #AC8EE3;
}

.hero__sidebar-subtitle--4 {
  color: #FFA34D;
}

.hero__sidebar-subtitle--5 {
  color: #3DC47E;
}

.hero__sidebar-text {
  font-size: 14px;
  line-height: 20px;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.footer {
  min-height: 100vh;
}

.user {
  display: flex;
  align-items: center;
}

.user__img {
  width: 45px;
  height: 45px;
  margin-right: 10px;
  border-radius: 50%;
}

.user__info {
  display: flex;
  flex-direction: column;
}

.user__name {
  margin-bottom: 5px;
  font-size: 14px;
  color: #fff;
  mix-blend-mode: normal;
  opacity: 0.85;
}

.user__job {
  font-size: 12px;
  color: #fff;
  opacity: 0.4;
}

.button {
  padding: 10px 40px 9px 20px;
  min-width: 154px;
  height: 40px;
  font-weight: 900;
  font-size: 14px;
  color: #fff;
  background-color: #4592ff;
  border-radius: 20px;
}

.button__arrow {
  position: relative;
  text-align: left;
}

.button__arrow::after {
  content: "";
  position: absolute;
  right: 22px;
  top: 50%;
  transform: translatey(-50%);
  width: 15px;
  height: 7px;
  background-image: url("../img/btn-arrow.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.article__list {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
  background-color: #30363D;
  border-radius: 10px;
  overflow: hidden;
}

.article__item {
  position: relative;
}

.article__item::after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  width: 1px;
  height: 80px;
  background-color: #4b5157;
}

.article__item:last-child::after {
  display: none;
}

.article__link {
  position: relative;
  display: flex;
  align-items: center;
  padding: 30px 25px 35px;
}

.article__link:hover {
  background-color: #3b434b;
}

.article__title {
  font-weight: 900;
  font-size: 14px;
  max-width: 154px;
  line-height: 20px;
  color: #fff;
  margin-right: 15px;
}

.article__img {
  width: 65px;
  height: 65px;
  object-fit: cover;
}

.grid__list {
  display: grid;
  gap: 30px;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 170px;
}

.grid__item {
  background-color: #fff;
  border-radius: 10px;
}

.grid__item--big {
  grid-column: span 2;
  grid-row: span 2;
  background-image: url("../img/grid-big.png");
  background-repeat: no-repeat;
  background-position: right 15px;
}

.grid__item--mid {
  grid-row: span 2;
}

.grid__item--social {
  padding: 30px 36px 40px 37px;
  text-align: center;
}

.grid__info {
  max-width: 237px;
}

.grid__subtitle {
  margin-bottom: 23px;
  font-size: 12px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: #6E99AE;
}

.grid__title {
  margin-bottom: 25px;
  font-family: 'Roboto Slab', sans-serif;
  font-size: 25px;
  line-height: 30px;
  color: #262D33;
}

.grid__title--small {
  font-size: 20px;
  line-height: 25px;
}

.grid__text {
  font-size: 14px;
  line-height: 20px;
}

.grid__link {
  position: relative;
  display: block;
  height: 100%;
  width: 100%;
}

.grid__link--big {
  padding: 35px;
  padding-bottom: 90px;
}

.grid__user {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  padding: 12px 35px 13px;
  border-top: 1px solid #D9DADB;
}

.grid__user-info {
  display: flex;
  margin-right: 16px;
}

.grid__user-img {
  width: 30px;
  height: 30px;
  margin-right: 10px;
  border-radius: 50%;
}

.grid__user-name {
  margin-right: 5px;
  font-weight: 900;
  font-size: 14px;
  color: #262D33;
}

.grid__user-text {
  font-size: 14px;
}

.comment {
  display: flex;
  align-items: center;
}

.comment__img {
  width: 13.5px;
  height: 13.5px;
  margin-right: 6px;
}

.comment__counter {
  font-size: 12px;
  color: #939699;
}

.card__link {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 25px;
  padding-top: 29px;
  padding-bottom: 30px;
}

.card__title {
  margin-bottom: 9px;
  font-family: 'Roboto Slab', sans-serif;
  font-size: 16px;
  color: #262D33;
}

.card__text {
  margin-bottom: 7px;
  font-size: 14px;
  line-height: 20px;
}

.card__date {
  font-size: 12px;
  color: #939699;
  margin-top: auto;
}

.social {
  display: flex;
  justify-content: space-between;
}

.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  width: 50px;
  border-radius: 50%;
}

.social__link--facebook {
  background-color: #4267B2;
}

.social__link--twitter {
  background-color: #1DA1F2;
}

.social__link--youtube {
  background-color: #FF0000;
}

.social__img {
  width: 20px;
  height: 20px;
}

.banner {
  position: relative;
  margin: 60px 0;
  padding-top: 51px;
  padding-bottom: 54px;
  background-image: url("../img/banner.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  text-align: center;
  z-index: 10;
}

.banner::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, rgba(64, 48, 61, 0.35), rgba(64, 48, 61, 0.35));
  z-index: -1;
}

.banner__title {
  max-width: 80%;
  margin-bottom: 25px;
  margin-right: auto;
  margin-left: auto;
  font-family: "Roboto Slab", sans-serif;
  font-size: 40px;
  line-height: 50px;
  color: #fff;
}

.banner__button {
  min-width: 161px;
}
